/**
 * Clase que representa un luchador
 */
export abstract class Figther {
  constructor(public nombre: string, public peso_altura: string[], protected universo: string, protected ataque: number, 
    protected defensa: number, protected velocidad: number, protected hit_points: number, protected frase_estelar: string, protected tipo?: string) {
  }

  abstract getUniverso() : string;
  abstract getAtaque() : number;
  abstract getDefensa() : number;
  abstract getVelocidad() : number;
  abstract getHP() : number;
  abstract getFraseEstelar() : string;
}